'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@3d-measurement-app/ui';

export default function CompanyRedirectPage() {
  const { user, loading } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (loading) return;
    
    if (!user) {
      router.push('/login');
      return;
    }

    // Redirect to user's company subdomain
    if (user.role === 'COMPANY_ADMIN' || user.role === 'COMPANY_USER') {
      if (user.company?.subdomain) {
        router.push(`/company/${user.company.subdomain}/orders`);
      } else {
        // User has company role but no company assigned
        router.push('/login?error=no-company');
      }
    } else {
      // Non-company users shouldn't access company dashboard
      router.push('/login?error=unauthorized');
    }
  }, [user, loading, router]);

  return (
    <div className="min-h-screen flex items-center justify-center">
      <div className="text-center">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-4"></div>
        <p>Redirecting to your company dashboard...</p>
      </div>
    </div>
  );
}